import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { DirectLine } from 'botframework-directlinejs';
import * as $ from 'jquery';
import { NgxAutoScrollModule } from "ngx-auto-scroll";
// import { texttospeech } from "../assets/javascript/demo"; 
declare var postActivity: any;
declare var message_from_directline: any;
declare var directLine: any;
declare var activity$: any;
const { webkitSpeechRecognition } = (window as any);
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

// export interface IWindow extends Window {
//   webkitSpeechRecognition: any;
//   const {webkitSpeechRecognition} : IWindow = <IWindow>window;
//   this.recognition = new webkitSpeechRecognition();
// }
export class AppComponent implements OnInit {
  user_message: any;
  message_speak = new SpeechSynthesisUtterance();
  total_message = [];
  cards = [];
  message_from_drestline: any;
  voices = window.speechSynthesis.getVoices();
  directLine: any;
  text_ui = [];
  OnLine = navigator.onLine;
  type_key: string;
  recognizing = false;
  ignore_onend: any;
  start_timestamp: any;
  recognition = new webkitSpeechRecognition();
  current_style: any;
  first_char = /\S/;
  window: any;
  muted = false;
  ngOnInit(): void {
    this.directLine = new DirectLine({
      secret: 'j5fFsThqPb4.cwA.0A4.6kz919MKX331var99c7UlzDzFMow4IHwoeyAlCO8wDs',
      webSocket: true,
      pollingInterval: 1000
    });
    this.message_speak.lang = 'en-US';
    this.message_speak.rate = 1.0;
    this.message_speak.onstart = function (event) {
      $('#lady').attr("src", "../assets/images/avatar.gif");
    }

    this.message_speak.onend = function (event) {
      $('#lady').attr("src", "../assets/images/avatar_3.jpg");
    }
    this.directLine.activity$.filter(activity => activity.type === 'message').subscribe(
      message => {
        var voices_local = window.speechSynthesis.getVoices();
        console.log(message,voices_local);
        if (message.from['name'] == "rohan_bot_asking") {
          if (message.text) {
            this.text_ui = [];
            this.message_from_drestline = message.text;
            console.log("message from rohan_bot_asking :-", this.message_from_drestline);
            console.log(this.message_from_drestline[0]);
            if (!this.muted) {
              if (this.message_from_drestline[0] == '_') {
                this.message_from_drestline = this.message_from_drestline.substr(1).slice(0, this.message_from_drestline.length)
              }
              else {
                // var message_speak_local = new texttospeech({
                //   text:this.message_from_drestline
                // });
                // message_speak_local.speech_speak();
                var message_speak_local = new SpeechSynthesisUtterance();
                message_speak_local.voice = voices_local[1];
                message_speak_local.lang = voices_local[1].lang;
                message_speak_local.rate = this.message_speak.rate;
                message_speak_local.onstart = this.message_speak.onstart;
                message_speak_local.onend = this.message_speak.onend;
                message_speak_local.text = this.message_from_drestline;
                window.speechSynthesis.speak(message_speak_local);
                // responsiveVoice.speak("hello world");
              }
            }
            this.text_ui.push({ name: this.message_from_drestline, side: 'bot' });
            this.total_message.push({ message_ui: this.text_ui });
          }
          if (message.attachments) {
            this.text_ui = [];
            console.log(message.attachments[1])
            let i;
            for (i = 0; i < message.attachments.length; i++) {
              if (!this.muted) {
                var message_speak_local = new SpeechSynthesisUtterance();
                message_speak_local.voice = voices_local[1];
                message_speak_local.lang = voices_local[1].lang;
                message_speak_local.rate = this.message_speak.rate;
                message_speak_local.onstart = this.message_speak.onstart;
                message_speak_local.onend = this.message_speak.onend;
                message_speak_local.text = message.attachments[i].content.title;
                window.speechSynthesis.speak(message_speak_local);
              }
              this.text_ui.push({ title: message.attachments[i].content.title, text: message.attachments[i].content.text, url: message.attachments[i].content.images[0].url })
            }
            this.total_message.push({ attachment: this.text_ui });
          }
          if (message.suggestedActions) {
            this.text_ui = [];
            console.log(this.text_ui)
            console.log(message.suggestedActions.actions.length)
            let i;
            for (i = 0; i < message.suggestedActions.actions.length; i++) {
              if (!this.muted) {
                var message_speak_local = new SpeechSynthesisUtterance();
                message_speak_local.voice = voices_local[1];
                message_speak_local.lang = voices_local[1].lang;
                message_speak_local.rate = this.message_speak.rate;
                message_speak_local.onstart = this.message_speak.onstart;
                message_speak_local.onend = this.message_speak.onend;
                message_speak_local.text = message.suggestedActions.actions[i].title;
                window.speechSynthesis.speak(message_speak_local);
              }
              this.text_ui.push({ title: message.suggestedActions.actions[i].title, text: message.suggestedActions.actions[i].value })
            }
            this.total_message.push({ buttons: this.text_ui });
          }
        }
        console.log(this.total_message);
      });
  }
  send() {
    window.speechSynthesis.cancel();
    this.user_message = $('.input_value').val();
    if (this.user_message != "") {
      this.directline_message(this.user_message);
    }
    $(".icon2").css("display", "none");
    $(".start").css("display", "none");
    $(".input_value").val("");
  }
  readmore(text) {
    window.speechSynthesis.cancel();
    this.directline_message(text);
  }
  directline_message(text_input) {
    if (this.recognizing) {
      this.recognition.stop();
    }
    this.type_key = null;
    console.log(this.type_key);
    this.internet_connection();
    this.text_ui = [];
    this.text_ui.push({ name: text_input, side: 'user' });
    this.total_message.push({ message_ui: this.text_ui });
    console.log(text_input)
    this.directLine.postActivity({
      from: { id: 'arpit', name: 'You' }, // required (from.name is optional)
      type: 'message',
      text: text_input,
    }).subscribe(
      id => console.log("Posted activity, assigned ID ", id),
      error => console.log("Error posting activity", error)
    );
  }
  internet_connection() {
    this.OnLine = navigator.onLine;
    if (this.OnLine)
      console.log('internet');
    else
      alert('no internet');
  }
  keyboard_press() {
    this.type_key = 'TYPING....';
    console.log(this.type_key);
  }
  mic(event) {
    console.log('event', event)
    this.recognizing = true;
    this.recognition.lang = "en_US";
    this.recognition.start();
    this.recognition.continuous = true;
    this.recognition.interimResults = true;
    var temp = this;
    var final_transcript = "";
    this.recognition.onresult = function (event) {
      var interim_transcript = '';
      for (var i = event.resultIndex; i < event.results.length; ++i) {
        if (event.results[i].isFinal) {
          final_transcript += event.results[i][0].transcript;
        }
        else {
          interim_transcript += event.results[i][0].transcript;
        }
      }
      console.log(final_transcript)
      if (final_transcript) {
        console.log("*", final_transcript);
        $('.input_value').val(final_transcript);
        // temp.directline_message(final_transcript);
        temp.send();
        final_transcript = "";

        temp.recognition.stop();
        return;
      }
      final_transcript = "";
    };
    this.ignore_onend = false;
    console.log('ignore');
  }
  showButtons(style) {
    if (style == this.current_style) {
      return;
    }
    this.current_style = style;
  }
  cross() {
    $(".sidebox").css("width", "0vw");
    $(".lady").css("width", "0vw");
    $(".menu").css("display", "block");
    $(".chatbox").css("left", "14vw");
    $(".icon6").css("display", "none");
    $(".amanda").css("display", "none");
  }

  cross_no() {
    $(".sidebox").css("width", "25vw");
    $(".lady").css("width", "22vw");
    $(".menu").css("display", "none");
    $(".chatbox").css("left", "28vw");
  }
  mute() {
    window.speechSynthesis.cancel();
    console.log('hello@@');
    this.muted = true;
    $(".icon4").css("display", "none");
    $(".icon7").css("display", "block");
  }
  volume() {
    this.muted = false;
    $(".icon4").css("display", "block");
    $(".icon7").css("display", "none");
  }
  onKeydown(event) {
    this.send();
  }
}


